@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;700;900&display=swap);
.menu{
    position: fixed;
    top: 0px;
    right: 50px;
    cursor: pointer;
    z-index: 20;
}

.hamburguer {
    position: fixed;
    display: block;
    background: rgb(250, 247, 247);
    width: 22px;
    height: 3px;
    top: 25px;
    left: 90.5%;
    transition: 0.3s ease-in-out;
    z-index: 2;

}

.hamburguer:before,
.hamburguer:after{
    background: rgb(252, 250, 250);
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    transition: 0.3s ease-in-out;
}

.hamburguer:before{
    top: -7px;
}

.hamburguer:after{
    bottom: -7px;
}

input {
    display: none;
    width: 200px;
    height: 200px;
}

input:checked ~ label .hamburguer{
    transform: rotate(-45deg);
}

input:checked ~ label .hamburguer:before{
    transform: rotate(-90deg);
    top: 0;
}

input:checked ~ label .hamburguer:after{
    transform: rotate(-90deg);
    bottom: 0;
}

.lateral{
    background-color: #000;
    position: absolute;
    height: 100vh;
    width: 100%;
    display: block;
    top: 0px;
    transition: all .2s ease-in-out;
    left: 100%;
}


input:checked ~ .lateral{
    transform: translateX(-100%);
}

.items-menu{
    position: relative;
    top: 50px;
}

.item-menu{
    margin-top: 30px;
    margin-left: 40px;
    font-size: 16px;
}

.item-menu-cinza{
    color: #b2b3b2;
}

.item-menu-logo{
    margin-top: 620px;
    margin-left: 15px;
}

ul{
    display: none;
    list-style-type: none;
    margin-top: 30px;
}
ul li{
    float: left;
    font-size: 1rem;
    margin-left: 30px;
    font-weight: 700;
}

@media(min-width: 768px){
    .lateral{
